          text: "주요설비현황",
          active: true,
        },
      ],
      toggleMode: "",
      selectedItemIndex:-1,
      selectedItem:"",
    };
  },
  computed: {
    isTurbinVisible() {
      return (this.toggleMode.includes("turbin")||this.toggleMode === "")
    },
    isBoilerVisible() {
      return (this.toggleMode.includes("boiler")||this.toggleMode === "")
    },
    isStorageVisible() {
      return (this.toggleMode.includes("storage")||this.toggleMode === "")
    },
    isSensorDetailVisible() {
      return (this.toggleMode.includes("sensor"))
    },
    isChartVisible() {
      return (this.toggleMode !== "" && !this.toggleMode.includes("sensor"))
    }
  },
  methods: {
    toggleClass(tagName, selId=-1, selItem='') {
      console.log("toggleClass:"+tagName+"  :"+this.toggleMode)
      this.selectedItemIndex=selId
      this.selectedItem=selItem
      if (tagName.includes("sensor")) {
          //this.toggleMode = (this.toggleMode.includes("sensor"))?tagName.replace('sensor-',''):tagName
          this.toggleMode = (this.toggleMode!="")?"":tagName
      } else if (tagName==="turbin") {
          this.toggleMode = (this.toggleMode!="")?"":tagName
      } else if (tagName === "boiler") {
          this.toggleMode = (this.toggleMode==="boiler")?"":tagName
      } else if (tagName === "storage") {
          this.toggleMode = (this.toggleMode==="storage")?"":tagName
      }
      console.log("toggleMode :"+this.toggleMode)
      // } else if (tagName.includes("turbin")) {
      //     this.toggleMode = (this.toggleMode.includes("turbin"))?"":tagName
      // } else if (tagName === "boiler") {
      //     this.toggleMode = (this.toggleMode.includes("boiler"))?"":tagName
      // } else if (tagName === "storage") {
      //     this.toggleMode = (this.toggleMode.includes("storage"))?"":tagName
      // }
      //toggleClass
    }
  },    
}
