<script>
/**
 외부 javascript를 Vue에 추가하는 방법
 https://stackoverflow.com/questions/45047126/how-to-add-external-js-scripts-to-vuejs-components
 https://www.npmjs.com/package/vue-plugin-load-script
 */

//import { $, jQuery } from "jquery";
//
//require('flot-charts')
//import { FlotChart } from "./data";
import { overviewChart } from "./overview-chart";
//import { VueApexCharts } from 'apexcharts'

export default {
  // components: {
  //   VueApexCharts 
  // },
  props: {
    //
  },
  data() {
    return {
      overviewChart: overviewChart,
      itemCount: 0,
      timer: '',
      curTime: 0,
      curVal: {},
    }
  },
  computed: {
    //
  },
  methods: {
    addNewValues() {
      let tempTime = this.curTime
      this.overviewChart.series.forEach( series => {
        this.curTime = tempTime
        if (series.data.length >= 100) {
          series.data = series.data.slice(1)
        }
        series.data.push([this.getNextTime(),this.getNextValue(series.name)])
      })
    },
    getNextValue(key) {
      let tempVal = 0
      if (this.curVal[key]==undefined) {
        tempVal = Math.random() + 30;
      } else {
        tempVal = this.curVal[key]
      console.log("1:::"+this.curVal[key]+":"+tempVal)
        if (tempVal>60) {
          tempVal -= Math.random() * 10
        } else if (this.curVal[key]<10) {
          tempVal += Math.random() * 10
        } else {
          tempVal += Math.random() * 10 - 5
        }
      }

      this.curVal[key] = tempVal
      console.log("2:::"+this.curVal[key]+":"+tempVal)
      return this.curVal[key]
    },
    getNextTime() {
      this.curTime +=3000
      return this.curTime
    }
  },

  mounted() {

    var me = this
    this.curTime=new Date().getTime() - 3000*100
    let tempTime = this.curTime
    this.overviewChart.series.forEach( series => {
      this.curTime = tempTime
      series.data.splice(0, series.data.length)
      for (let i = 0; i < 100; i++) {
        series.data.push([this.getNextTime(),this.getNextValue(series.name)])
      }
    })
    console.log(this.overviewChart.series)
    this.timer = setInterval( () => {
      this.addNewValues()
      me.$refs.realtimeChart.updateSeries(this.overviewChart.series)
    }, 3000)
  }, 
  destroyed() {
    clearInterval(this.timer)
  }
};
</script>

<template>
  <div id="chartid">
    <!-- <h4 class="card-title mb-3">주요설비 현황 차트</h4> -->
    <!-- <div id="flotRealTime" style="height: 160px;" class="flot-chart"></div> -->
    <div>
      <div id="overview-chart" class="apex-charts" dir="ltr">
        <!-- <div class="toolbar button-items text-center">
          <button type="button" class="btn btn-light btn-sm" id="one_month">1M</button>
          <button type="button" class="btn btn-light btn-sm" id="six_months">6M</button>
          <button type="button" class="btn btn-light btn-sm active" id="one_year">1Y</button>
          <button type="button" class="btn btn-light btn-sm" id="all">ALL</button>
        </div> -->
        <apexchart
          ref="realtimeChart"
          class="apex-charts"
          type="area"
          height="275"
          dir="ltr"
          :series="overviewChart.series"
          :options="overviewChart.chartOptions"
        ></apexchart>
      </div>
    </div>
    
  </div>
</template>
