<script>
import {
    OveviewChart,
    activitiesData
} from "./data";
/**
 * Wallets component
 */
export default {
    components: {
        //
    },
    props: {
        title: {
            type: String, 
            required: true
        }
    },
    data() {
        return {
            OveviewChart: OveviewChart,
            activitiesData: activitiesData,
            items: [{
                    text: "Crypto",
                    href: "/",
                },
                {
                    text: "Wallets",
                    active: true,
                },
            ],
            totalRows: 1,
            currentPage: 1,
            perPage: 10,
            pageOptions: [10, 25, 50, 100],
            filter: null,
            filterOn: [],
            sortBy: "id",
            sortDesc: false,
            fields: [{
                    key: "id",
                    label:'센서No',
                    sortable: true,
                },
                {
                    key: "date",
                    label:'발생시간',
                    sortable: true,
                },
                {
                    key: "type",
                    label:'측정단위',
                    sortable: true,
                },
                {
                    key: "amount",
                    label:"측정값",
                    sortable: true,
                },
            ],
        };
    },
    computed: {
        /**
         * Total no. of records
         */
        rows() {
            return this.activitiesData.length;
        },
    },
    mounted() {
        // Set the initial number of items
        this.totalRows = this.items.length;
    },
    methods: {
        /**
         * Search the table data with search input
         */
        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },
    },
};
</script>

<template>
<Layout>
    <div class="row">

        <div class="col-xl-12">

            <div class="card">
                <div class="card-body">
                    <h4 class="card-title mb-3">센서 현황 ({{ title }})</h4>

                    <div>
                        <div id="overview-chart" class="apex-charts" dir="ltr"></div>
                        <apexchart class="apex-charts" height="240" type="line" dir="ltr" :series="OveviewChart.series" :options="OveviewChart.chartOptions"></apexchart>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!-- end row -->
    <div class="row">
      <div class="col-12 grid-margin">
        <div class="card">
          <div class="card-body">
            <h4 class="card-title">센서 상세내역 ({{ title }})</h4>
            <b-tabs justified class="col-12 tab-solid tab-solid-primary">
                <b-tab title="설비별" active>
                    <div class="d-sm-flex pb-2 mb-2 border-bottom justify-content-between">
                    <div class="d-flex align-items-center justify-content-between">
                        <h5 class="page-title mb-n2">전체 센서</h5>
                        <p class="mt-2 mb-n1 ml-3 text-muted">230 개</p>
                    </div>
                    <form class="d-lg-flex pt-2 pt-md-0 align-items-stretch justify-content-between">
                        <input type="text" class="form-control" placeholder="찾기..">
                        <button type="submit" class="btn btn-gradient-success no-wrap ml-0 ml-lg-4 mt-1 mt-lg-0" style="white-space:pre;">검색</button>
                    </form>
                    </div>
                    <div class="table-responsive table-hover mb-0">
                        <b-table :items="activitiesData" id="table-list" 
                            responsive 
                            :per-page="perPage" 
                            :current-page="currentPage" 
                            :fields="fields" 
                            :sort-by.sync="sortBy" 
                            :sort-desc.sync="sortDesc">
                        </b-table>
                        <b-pagination
                        v-model="currentPage"
                        :total-rows="rows"
                        :per-page="perPage"
                        aria-controls="table-list"
                        align="right">
                        </b-pagination>
                    </div>
                </b-tab>
                <b-tab title="IoT감지기">
                    <div class="row">
                        
                    </div>
                </b-tab>
            </b-tabs>

          </div>
        </div>
      </div>
    </div>

</Layout>
</template>
