<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    },
    id: {
      type: String, 
      required: true
    },
    items: Array,
    needNarrow: {
      type: Boolean,
      default: true
    },
    alarmLevel: {
      type: String,
      default: "muted"
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    tooltipId() {
      return  (mainId, index) => {
        return `tt_${mainId}_${index}`
      }
    },
    flashAlarm() {
      return (this.alarmLevel ==="danger" || this.alarmLevel ==="warning")
    },
    flashAlarm2() {
      let ret = (this.alarmLevel ==="danger")
      console.log('ret'+ret)
      return (this.alarmLevel ==="danger")
    },
    badgeClass() {
      return 'badge font-size-10 mr-1 badge-soft-'+(this.alarmLevel ==="danger" || this.alarmLevel ==="warning"?this.alarmLevel:'success')
      +(this.alarmLevel ==="danger" || this.alarmLevel ==="warning"?' blink-me':'')
    }
  }
}
</script>

<template>
  <div :class="'sensor-box facility-status-item-row '+(flashAlarm?'item-warning':'')+(selected?' row-selected':'')">
    <div class="item-title">{{ title }}</div>
    <div v-for="(item, index) in items" :key="index">
      <div class="item-subtitle" :id="tooltipId(id,index)">
        {{ item.title }}</div>
      <div class="item-subvalue">
        {{ item.value }}
        <i :class="flashAlarm2?'mdi mdi-alert font-size-14':''"></i>
      </div>
      <b-tooltip :target="tooltipId(id,index)" v-if="item.tooltip">{{ item.tooltip }}</b-tooltip>
    </div>
  </div>
</template>
